export const httpGetPharmacyOrderDetail = (id: string) =>
  request.get<AdminOrder.OrderDetailRes, any>(`${prefix}pharmacyOrder/getOrderDetail/${id}`);

/**
 * 订单查询-订单明细导出
 * @param id 订单id
 * @returns
 */
export const httpOrderListExport = (params: any) =>
  request.post(`${prefix}adminOrder/orderListExport`, params, {
    // @ts-ignore
    isFile: true,
    headers: { 'content-type': 'application/json; charset=utf-8' },
    responseType: 'blob',
  });

