/* eslint-disable indent */
import React, { useState } from 'react';
import {
  Badge,
  Button,
  Checkbox,
  Col,
  Pagination,
  Popover,
  Row,
  Select,
  Space,
  Table,
} from '@sinohealth/butterfly-ui-antd/lib';
import { QuestionCircleFilled, RedoOutlined, SettingOutlined } from '@ant-design/icons';
import styles from './index.less';
import DraggableModal from '@/components/DraggableModal';

const { Option } = Select;

type OrderTableProps = {
  dataSource: any;
  total: number;
  columns: any;
  current: number;
  loading: boolean;
  onReload: () => void;
  onPage: (page: number, pageSize: number) => void;
  onExport: (type: string) => void;
  onSelect: (val: string[]) => void;
  onSort: (val: any) => void;
};

/**
 * 订单查询-订单表格数据
 * @returns
 */
const OrderTable: React.FC<OrderTableProps> = (props) => {
  const {
    dataSource,
    total = 0,
    columns,
    current = 1,
    loading,
    onReload,
    onPage,
    onExport,
    onSelect,
    onSort,
  } = props;
  const [visDraggable, setVisDraggable] = useState(false);
  const [visPopover, setvIsPopover] = useState(false);
  const [selectColumn, setSelectColumn] = useState<any>([]);
  const [exportType, setExportType] = useState('xlsx');
  // 切换页面
  const handleOnChangePagination = (page: number, pageSize: number) => {
    console.log(page, pageSize);
    onPage(page, pageSize);
  };

  const popoverContent = () => (
    <div className={styles['popover-container']}>
      <h4 className={styles['popover-title']}>列表设置 - 更多列表数据项目</h4>
      <div className={styles['popover-wrapper']}>
        <span className={styles['popover-label']}>更多项目：</span>
        <Checkbox.Group style={{ width: '100%' }} onChange={(e) => setSelectColumn(e)}>
          <Row gutter={[0, 24]}>
            <Col span={24}>
              <Checkbox value="deliveryAmount">运费</Checkbox>
            </Col>
            <Col span={24}>
              <Checkbox value="chainName">订单来源</Checkbox>
            </Col>
            <Col span={24}>
              <Checkbox value="discountAmount">扣减金额</Checkbox>
            </Col>
            <Col span={24}>
              <Checkbox value="realAmount">实收金额</Checkbox>
            </Col>
          </Row>
        </Checkbox.Group>
      </div>
      <div className={styles['popover-tool']}>
        <span />
        <Space size="middle">
          <Button onClick={() => setvIsPopover(false)}>取消</Button>
          <Button
            type="primary"
            onClick={() => {
              setvIsPopover(false);
              onSelect(selectColumn);
            }}
          >
            确定
          </Button>
        </Space>
      </div>
    </div>
  );

  return (
    <div className={styles['order-table-container']}>
      {/* 表头 */}
      <div className={styles['order-table-header']}>
        <h4 className={styles['table-header-title']}>处方订单列表</h4>
        <Space size="middle">
          {/* <RedoOutlined
            style={{ cursor: 'pointer', color: 'rgba(0,0,0,0.25)' }}
            onClick={onReload}
          /> */}
          <span className={styles['reload-icon']} onClick={onReload} />
          <Popover
            overlayClassName={styles['popover-father']}
            placement="bottomRight"
            arrowPointAtCenter
            visible={visPopover}
            mouseLeaveDelay={0.3}
            trigger="click"
            content={popoverContent()}
          >
            <SettingOutlined
              style={{ cursor: 'pointer', color: '#217BA0' }}
              onClick={() => setvIsPopover(!visPopover)}
            />
          </Popover>
          <Button type="primary" onClick={() => setVisDraggable(true)}>
            批量导出
          </Button>
        </Space>
      </div>
      {/* 表格数据 */}
      <div className={styles['table-container']} onClick={() => setvIsPopover(false)}>
        <Table
          rowKey="id"
          loading={loading}
          pagination={false}
          scroll={{ x: 'calc(700px + 50%)' }}
          columns={columns}
          dataSource={dataSource}
          onChange={(pagination, filters, sorter: any, extra) => {
            // 处理排序
            const t: any = {
              createTime: 'create_time',
              receiveTime: 'receive_time',
              realAmount: 'real_amount',
              signTime: 'sign_time',
            };
            const orders: AdminOrder.OrdersType[] = sorter.order
              ? [
                  {
                    property: t[sorter.field],
                    direction: sorter.order === 'ascend' ? 'asc' : 'desc',
                  },
                ]
              : [{ property: 'create_time', direction: 'desc' }];

            onSort(orders);
          }}
        />
      </div>
      {/* 翻页 */}
      <div className={styles['pagination-container']}>
        <span />
        <Pagination
          total={total}
          current={current}
          showSizeChanger
          showQuickJumper
          showTotal={(num) => `共 ${num} 条`}
          onChange={handleOnChangePagination}
        />
      </div>
      {/* 批量导出 */}
      <DraggableModal
        visible={visDraggable}
        closable={false}
        width="420px"
        icon={<QuestionCircleFilled style={{ color: '#217BA0', fontSize: 24, marginRight: 16 }} />}
        tipTitle="请选择导出格式"
        onCancel={() => setVisDraggable(false)}
        onOk={() => {
          onExport && onExport(exportType);
          const timer = setTimeout(() => {
            clearTimeout(timer);
            setVisDraggable(false);
          }, 500);
        }}
      >
        <div className={styles['out-put']}>
          {/* <div className={styles['out-title']}></div> */}
          <div className={styles['out-form']}>
            <span className={styles['out-label']}>导出格式：</span>
            <Select
              defaultValue="xlsx"
              value={exportType}
              style={{ width: 160 }}
              onChange={(type) => setExportType(type)}
            >
              <Option value="xlsx">xlsx</Option>
              <Option value="xls">xls</Option>
              <Option value="csv">csv</Option>
            </Select>
          </div>
        </div>
      </DraggableModal>
    </div>
  );
};

export default OrderTable;
