import React, { useRef, useState } from 'react';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import { Modal } from '@sinohealth/butterfly-ui-antd';
import type { ModalProps } from '@sinohealth/butterfly-ui-antd';
import Draggable, { DraggableData, DraggableEvent } from 'react-draggable';
import './index.less';

interface DraggableModalProps extends ModalProps {
  icon?: any;
  tipTitle?: string;
  orderType?: string;
}

/**
 * 可拖拽的modal
 * @returns
 */
const DraggableModal: React.FC<DraggableModalProps> = (props: any) => {
  const draggleRef = useRef() as any;
  const [disabled, setDisabled] = useState(true);
  const [bounds, setBounds] = useState({ left: 0, top: 0, bottom: 0, right: 0 });

  const onStart = (event: DraggableEvent, uiData: DraggableData) => {
    const { clientWidth, clientHeight } = window.document.documentElement;
    const targetRect = draggleRef?.current?.getBoundingClientRect();
    if (!targetRect) {
      return;
    }
    setBounds({
      left: -targetRect.left + uiData.x,
      right: clientWidth - (targetRect.right - uiData.x),
      top: -targetRect.top + uiData.y,
      bottom: clientHeight - (targetRect.bottom - uiData.y),
    });
  };

  function tipTitle() {
    return (
      <div className="dragg-tip-title">
        {props.icon ? (
          props.icon
        ) : (
          <ExclamationCircleOutlined style={{ color: '#FFC100', fontSize: 24 }} />
        )}
        <div className="tip-title">{props?.tipTitle}</div>
      </div>
    );
  }

  return (
    <Modal
      centered
      {...props}
      title={
        props?.title ? (
          <div
            style={{
              width: '100%',
              cursor: 'move',
            }}
            onMouseOver={() => {
              if (disabled) {
                setDisabled(false);
              }
            }}
            onMouseOut={() => {
              setDisabled(true);
            }}
            onFocus={() => {}}
            onBlur={() => {}}
            // end
          >
            {props?.title || 'Draggable Modal'}
          </div>
        ) : null
      }
      visible={props?.visible}
      onOk={props?.onOk}
      onCancel={props?.onCancel}
      wrapClassName="draggable-modal"
      modalRender={(modal) => (
        <Draggable
          disabled={disabled}
          bounds={bounds}
          onStart={(event, uiData) => onStart(event, uiData)}
        >
          <div ref={draggleRef}>{modal}</div>
        </Draggable>
      )}
    >
      {props?.tipTitle ? tipTitle() : null}
      {props?.orderType ? (
        <div className="draggable-modal-order-type">{props?.orderType}</div>
      ) : null}
      {props?.children}
    </Modal>
  );
};

export default DraggableModal;
