import moment from 'moment';

const DATE_TIME_FORMAT = 'YYYY-MM-DD HH:mm';
const DATE_FORMAT = 'YYYY-MM-DD';

/**
 * 时间格式化 YYYY-MM-DD HH:mm
 * @param date
 * @param format YYYY-MM-DD HH:mm
 * @returns
 */
export function formatToDateTime(
  date: moment.MomentInput = undefined,
  format = DATE_TIME_FORMAT,
): string {
  return moment(date).format(format);
}

/**
 * 时间格式 YYYY-MM-DD
 * @param date moment 格式
 * @param format 格式
 * @returns string
 */
export function formatToDate(date: moment.MomentInput = undefined, format = DATE_FORMAT): string {
  return moment(date).format(format);
}

/**
 * 通过生日获取年龄
 * @param bir 生日
 * @returns
 */
export function getAgeByBirth(bir: string = '') {
  if (!bir) return '未知';
  return (
    bir?.includes(' ')
      ? moment.duration(moment().diff(bir.split(' ')[0])).years()
      : moment.duration(moment().diff(bir)).years()
  ).toString();
}

/**
 * 二进制流文件下载
 * @param content 二进制流
 * @param type 类型 pdf zip
 */
export const downloadXlsxOrCsv = (
  content: any,
  type: string = 'xlsx',
  name: string | number = new Date().valueOf(),
) => {
  const blob = new Blob([content.data]);
  const temp = content?.headers['content-disposition'];
  const filename = temp ? decodeURI(temp.split(';')[1].split('filename=')[1]) : name;
  const fileName = `${type ? `${filename}.${type}` : filename}`;

  if ('download' in document.createElement('a')) {
    // 非IE下载
    const elink = document.createElement('a');
    elink.download = fileName;
    elink.style.display = 'none';
    elink.href = window.URL.createObjectURL(blob);
    document.body.appendChild(elink);
    elink.click();
    window.URL.revokeObjectURL(elink.href); // 释放URL 对象
    document.body.removeChild(elink);
  }
};
const doHandleMonth = (month: any) => {
  let m = month;
  if (month.toString().length === 1) {
    m = `0${month}`;
  }
  return m;
};
const getStandardDateBeforeWeek = () => {
  const date = new Date();
  date.setDate(date.getDate() - 6);
  const year = date.getFullYear();
  let month: any = date.getMonth() + 1;
  let day: any = date.getDate();
  if (month < 10) {
    month = `0${month}`;
  }
  if (day < 10) {
    day = `0${day}`;
  }
  return `${year}-${month}-${day}`;
};
export function getTimeFrame(date: string) {
  const time: any = [];
  const str: any = date.replace(/\s+/g, '');
  const day: any = new Date();
  if (str === '最近1天') {
    time[0] = formatToDate(`${day.getFullYear()}-${day.getMonth() + 1}-${day.getDate()}`);
    time[1] = formatToDate(`${day.getFullYear()}-${day.getMonth() + 1}-${day.getDate()}`);
  } else if (str === '最近1周') {
    time[0] = getStandardDateBeforeWeek(); // 获取7天前的日期
    time[1] = formatToDate(`${day.getFullYear()}-${day.getMonth() + 1}-${day.getDate()}`);
  } else if (str === '最近1月') {
    const now = new Date();
    const year = now.getFullYear();
    const month: any = now.getMonth() + 1;
    const days = now.getDate();
    time[1] = formatToDate(`${year}-${month}-${days}`);
    let lsTime: string = '';
    const nowMonthDay = new Date(year, month, 0).getDate();
    if (month - 1 <= 0) {
      // 如果是1月，年数往前推一年<br>
      lsTime = `${year - 1}-${12}-${days}`;
    } else {
      const lastMonthDay = new Date(year, parseInt(month, 10) - 1, 0).getDate();
      if (lastMonthDay < days) {
        // 1个月前所在月的总天数小于现在的天日期
        if (days < nowMonthDay) {
          // 当前天日期小于当前月总天数
          lsTime = `${year}-${month - 1}-${lastMonthDay - (nowMonthDay - days)}`;
        } else {
          lsTime = `${year}-${month - 1}-${lastMonthDay}`;
        }
      } else {
        lsTime = `${year}-${month - 1}-${days}`;
      }
    }
    time[0] = formatToDate(`${lsTime} `);
  } else if (str === '最近半年') {
    const cc = day.getTime();
    const halfYear = (365 / 2) * 24 * 3600 * 1000;
    const pastResult = cc - halfYear;
    const pastDate = new Date(pastResult);
    time[0] = formatToDate(
      `${pastDate.getFullYear()}-${pastDate.getMonth() + 1}-${pastDate.getDate()}`,
    );
    time[1] = `${formatToDate(new Date())} `;
  } else if (str === '最近1年') {
    time[0] = `${formatToDate(day.setFullYear(day.getFullYear() - 1))}`;
    time[1] = `${formatToDate(new Date())}`;
  }
  return time;
}
