import React, { useEffect, useState } from 'react';
import { useConfig } from './components/config';
import OrderTable from './components/OrderTable';
import SearchForm from './components/SearchForm';
import { httpOrderListExport } from '@/services/system';
import { useOrderListHttp } from './useOrderListHttp';

import { downloadXlsxOrCsv } from '@/utils/utils';

import styles from './index.less';

/**
 * 订单查询
 * @returns
 */
const OrderTracking: React.FC = () => {
  const { orderStatus, tableColumns, extraColumns, controlColumns } = useConfig();
  const [selectColumn, setSelectColumn] = useState<any>([...tableColumns, ...controlColumns]);
  const [columnList, setColumnList] = useState<any>([]);
  const [orderParams, setOrderParams] = useState({
    pageNo: 1,
    pageSize: 10,
    entity: {},
    orders: [{ property: 'create_time', direction: 'desc' }],
  });
  const { total, orderSource, chainSource, isLoading } = useOrderListHttp(orderParams);

  const httpOrderListExportReq = async (type: string) => {
    const res = await httpOrderListExport({
      fileType: type,
      orders: orderParams.orders,
      ...orderParams.entity,
    });
    downloadXlsxOrCsv(res);
  };

  const filterColumn = (ids: string[]) => {
    if (ids.length === 0) {
      setColumnList([...tableColumns, ...controlColumns]);
    } else {
      const d = extraColumns.filter((el: any) => ids.includes(el.key));
      console.log(d);
      setColumnList([...tableColumns, ...d, ...controlColumns]);
    }
  };

  useEffect(() => {
    filterColumn(selectColumn);
  }, [orderSource]);

  return (
    <div className={styles['order-tracking']}>
      <SearchForm
        options={orderStatus}
        chains={chainSource}
        onFinish={(entity) =>
          setOrderParams({
            ...orderParams,
            pageNo: 1,
            entity: {
              ...entity,
            },
          })
        }
      />
      <OrderTable
        total={total}
        columns={columnList}
        current={orderParams.pageNo}
        dataSource={orderSource}
        loading={isLoading}
        onPage={(page, pageSize) =>
          setOrderParams({
            ...orderParams,
            pageNo: page,
            pageSize,
          })
        }
        onReload={() => {
          setOrderParams({
            ...orderParams,
          });
        }}
        onExport={(type) => {
          httpOrderListExportReq(type);
        }}
        onSelect={(val) => {
          setSelectColumn(val);
          filterColumn(val);
        }}
        onSort={(orders) =>
          setOrderParams({
            ...orderParams,
            orders,
          })
        }
      />
    </div>
  );
};

export default OrderTracking;
