import { ReactChild, ReactFragment, ReactPortal, useEffect, useState } from 'react';
import { Badge, Button, Space } from '@sinohealth/butterfly-ui-antd/lib';
import { useDictHttp } from '@/hooks/useDict';
import { useNavigate } from 'react-router-dom';

// 订单状态

enum ORDER_STATUS {
  closed = 'closed',
  effective = 'effective',
  expired = 'expired',
  init = 'init',
}

enum ORDER_STATUS_COLOR {
  closed = '#FF5476',
  effective = '#7ED321',
  expired = '#FF5476',
  init = '#F5A623',
}
// 支付状态
enum PAY_STATUS {
  failed = 'failed',
  refund_fail = 'refund_fail',
  refund_success = 'refund_success',
  success = 'success',
  timeout = 'timeout',
  wait = 'wait',
}
enum PAY_STATUS_COLOR {
  failed = '#FF5476',
  refund_fail = '#FF5476',
  refund_success = '#FF5476',
  success = '#7ED321',
  timeout = '#FF5476',
  wait = '#F5A623',
}
// 发药状态
enum DISPENSE_STATUS {
  rejected = 'rejected',
  success = 'success',
  wait = 'wait',
}
enum DISPENSE_STATUS_COLOR {
  rejected = '#FF5476',
  success = '#7ED321',
  wait = '#F5A623',
}
// 配送状态
enum DELIVERY_STATUS {
  arrived = 'arrived',
  back_success = 'back_success',
  pick_up = 'pick_up',
  receive_success = 'receive_success',
  receive_wait = 'receive_wait',
  rejected = 'rejected',
  sign_failed = 'sign_failed',
  signed = 'signed',
  exception = 'exception',
  init = 'init',
  received = 'received',
  wait = 'wait',
}
enum DELIVERY_STATUS_COLOR {
  arrived = '#1791FF',
  back_success = '#7ED321',
  pick_up = '#7ED321',
  receive_success = '#7ED321',
  receive_wait = '#F5A623',
  rejected = '#FF5476',
  sign_failed = '#FF5476',
  signed = '#7ED321',
  exception = '#FF5476',
  init = '#F5A623',
  received = '#7ED321',
  wait = '#F5A623',
}

const COLORS = [
  'pink',
  'red',
  'yellow',
  'orange',
  'cyan',
  'green',
  'blue',
  'purple',
  'geekblue',
  'magenta',
  'volcano',
  'gold',
  'lime',
];

/**
 * 订单查询-表格列数据
 */
export const useConfig = () => {
  const { dictList } = useDictHttp();
  const navigate = useNavigate();

  /**
   * 高级搜索订单状态
   */
  const orderStatus = [
    {
      value: 'status',
      label: '订单状态',
      children: dictList?.advancedSearchStatus
        ? Object.keys(dictList?.advancedSearchStatus)?.map((el: any, index) => {
            return {
              value: el,
              label: <Badge color={COLORS[index]} text={dictList?.advancedSearchStatus[el]} />,
            };
          })
        : [],
    },
    {
      value: 'payStatus',
      label: '支付状态',
      children: dictList?.payStatus
        ? Object.keys(dictList?.payStatus)?.map((el: any, index) => {
            return {
              value: el,
              label: <Badge color={COLORS[index]} text={dictList?.payStatus[el]} />,
            };
          })
        : [],
    },
    {
      value: 'dispenseStatus',
      label: '发药状态',
      children: dictList?.dispenseStatus
        ? Object.keys(dictList?.dispenseStatus)?.map((el: any, index) => {
            return {
              value: el,
              label: <Badge color={COLORS[index]} text={dictList?.dispenseStatus[el]} />,
            };
          })
        : [],
    },
    {
      value: 'deliveryStatus',
      label: '物流状态',
      children: dictList?.OrderDeliveryStatusComb
        ? Object.keys(dictList?.OrderDeliveryStatusComb)?.map((el: any, index) => {
            return {
              value: el,
              label: <Badge color={COLORS[index]} text={dictList?.OrderDeliveryStatusComb[el]} />,
            };
          })
        : [],
    },
  ];

  const tableColumns: any = [
    {
      title: '患者姓名',
      dataIndex: 'patientName',
      key: 'patientName',
      width: 100,
      hide: false,
      render: (text: boolean | ReactChild | ReactFragment | ReactPortal | null | undefined) => (
        <span>{text || '-'}</span>
      ),
    },
    {
      title: '患者电话',
      dataIndex: 'patientPhone',
      key: 'patientPhone',
      width: 120,
      hide: false,
      render: (text: boolean | ReactChild | ReactFragment | ReactPortal | null | undefined) => (
        <span>{text || '-'}</span>
      ),
    },
    {
      title: '订单编号',
      dataIndex: 'id',
      key: 'id',
      width: 200,
      hide: false,
      render: (text: boolean | ReactChild | ReactFragment | ReactPortal | null | undefined) => (
        <span>{text || '-'}</span>
      ),
    },
    {
      title: '机构处方编号',
      dataIndex: 'sourceRefId',
      key: 'sourceRefId',
      width: 200,
      hide: false,
      render: (text: boolean | ReactChild | ReactFragment | ReactPortal | null | undefined) => (
        <span>{text || '-'}</span>
      ),
    },
    {
      title: '配送单号',
      dataIndex: 'deliveryNo',
      key: 'deliveryNo',
      width: 200,
      hide: false,
      render: (text: boolean | ReactChild | ReactFragment | ReactPortal | null | undefined) => (
        <span>{text || '-'}</span>
      ),
    },
    {
      title: '处方金额',
      dataIndex: 'prescriptionAmount',
      key: 'prescriptionAmount',
      width: 90,
      hide: false,
      render: (text: boolean | ReactChild | ReactFragment | ReactPortal | null | undefined) => (
        <span>{text !== null ? text : '-'}</span>
      ),
    },
    {
      title: '订单类型',
      dataIndex: 'deliveryCategory',
      key: 'deliveryCategory',
      width: 90,
      hide: false,
      render: (text: boolean | ReactChild | ReactFragment | ReactPortal | null | undefined) => (
        <span>{text || '-'}</span>
      ),
    },
    {
      title: '订单时间',
      dataIndex: 'createTime',
      key: 'createTime',
      width: 160,
      sorter: true,
      hide: false,
      render: (text: boolean | ReactChild | ReactFragment | ReactPortal | null | undefined) => (
        <span>{text || '-'}</span>
      ),
    },
    {
      title: '订单状态',
      dataIndex: 'status',
      key: 'status',
      width: 130,
      hide: false,
      render: (text: ORDER_STATUS) =>
        text ? (
          <Badge
            color={ORDER_STATUS_COLOR[text]}
            text={dictList?.orderStatus ? dictList?.orderStatus[text] : ''}
          />
        ) : (
          '-'
        ),
    },
    {
      title: '支付状态',
      dataIndex: 'payStatus',
      key: 'payStatus',
      width: 130,
      hide: false,
      render: (text: PAY_STATUS) =>
        text ? (
          <Badge
            color={PAY_STATUS_COLOR[text]}
            text={dictList?.payStatus ? dictList?.payStatus[text] : ''}
          />
        ) : (
          '-'
        ),
    },
    {
      title: '发药状态',
      dataIndex: 'dispenseStatus',
      key: 'dispenseStatus',
      width: 130,
      hide: false,
      render: (text: DISPENSE_STATUS) =>
        text ? (
          <Badge
            color={DISPENSE_STATUS_COLOR[text]}
            text={dictList?.dispenseStatus ? dictList?.dispenseStatus[text] : ''}
          />
        ) : (
          '-'
        ),
    },
    {
      title: '配送状态',
      dataIndex: 'deliveryStatus',
      key: 'deliveryStatus',
      width: 130,
      hide: false,
      render: (text: DELIVERY_STATUS) =>
        text ? (
          <Badge
            color={DELIVERY_STATUS_COLOR[text]}
            text={dictList?.deliveryStatus ? dictList?.deliveryStatus[text] : ''}
          />
        ) : (
          '-'
        ),
    },
  ];

  const extraColumns = [
    {
      title: '运费',
      dataIndex: 'deliveryAmount',
      key: 'deliveryAmount',
      width: 100,
      hide: true,
      render: (text: string) => (text !== null ? <span>{text}</span> : '-'),
    },
    {
      title: '订单来源',
      dataIndex: 'chainName',
      key: 'chainName',
      width: 130,
      hide: true,
      render: (text: string) => (text !== null ? <span>{text}</span> : '-'),
    },
    {
      title: '扣减金额',
      dataIndex: 'discountAmount',
      key: 'discountAmount',
      width: 100,
      hide: true,
      render: (text: string) => (text !== null ? <span>{text}</span> : '-'),
    },
    {
      title: '实收金额',
      dataIndex: 'realAmount',
      key: 'realAmount',
      width: 100,
      hide: true,
      render: (text: string) => (text !== null ? <span>{text}</span> : '-'),
    },
  ];

  const controlColumns = [
    {
      title: '操作',
      key: 'action',
      width: 100,
      align: 'right',
      fixed: 'right',
      render: (_: any, record: { id: any }) => (
        <Space size="middle">
          <Button type="link" onClick={() => navigate(`/prescrip/order/detail?id=${record.id}`)}>
            查看详情
          </Button>
        </Space>
      ),
    },
  ];

  return { orderStatus, tableColumns, extraColumns, controlColumns };
};
