import { useEffect, useState } from 'react';
import { httpGetOrdeList, httpListAllChain } from '@/services/system';

export const useOrderListHttp = (params: AdminOrder.GetListReq, type?: string) => {
  const [total, setTotal] = useState(0);
  const [isLoading, setIsLoading] = useState(false);
  const [orderSource, setOrderSource] = useState<AdminOrder.GetListRes[]>([]);
  const [chainSource, setChainSource] = useState<AdminOrder.GetListRes[]>([]);

  async function httpGetOrdeListReq() {
    setIsLoading(true);
    try {
      const res: any = await httpGetOrdeList(params);
      setTotal(res?.total);
      setIsLoading(false);
      if (type) {
        if (params.pageNo === 1) {
          setOrderSource(res?.records);
        } else {
          setOrderSource(orderSource.concat(res?.records));
        }
      } else {
        setOrderSource(res?.records);
      }
    } catch (err) {
      setIsLoading(false);
    }
  }

  async function httpListAllChainReq() {
    const res = await httpListAllChain();
    setChainSource(res);
    setIsLoading(false);
  }

  useEffect(() => {
    httpGetOrdeListReq();
    httpListAllChainReq();
  }, [params]);

  return { total, orderSource, isLoading, chainSource };
};

export default {
  useOrderListHttp,
};
