import React, { ReactNode, useEffect, useState } from 'react';
import {
  Button,
  Col,
  Form,
  Input,
  Row,
  DatePicker,
  Cascader,
  Select,
} from '@sinohealth/butterfly-ui-antd/lib';
import { DownOutlined, UpOutlined } from '@ant-design/icons';
import { formatToDate } from '@/utils/utils';
import styles from './index.less';

const { RangePicker } = DatePicker;
const { Option } = Select;

type SearchFormProps = {
  onFinish: (values: any) => void;
  chains: any;
  options: any;
};

/**
 * 订单查询-查询表单
 * @returns
 */
const SearchForm: React.FC<SearchFormProps> = (props) => {
  const { onFinish, options, chains } = props;
  const [form] = Form.useForm();
  const [expand, setExpand] = useState(false);
  const [maxHeight, setMaxHeight] = useState<string>('78px');

  const displayRender: any = (labels: any[], selectedOptions: { [x: string]: any }) =>
    labels.map((label, i) => {
      const option = selectedOptions[i];
      if (i === labels.length - 1) {
        return (
          <span key={option.value}>
            {typeof label === 'string' ? `${label}/` : `${label.props.text}`}
          </span>
        );
      }
      return (
        <span key={option.value}>
          {console.log(typeof label === 'string' ? `${label}` : `${label.props.text}`)}
          {typeof label === 'string' ? `${label}/` : `${label.props.text}`}
        </span>
      );
    });

  return (
    <div className={styles['order-table-container']} style={{ height: maxHeight }}>
      <Form
        form={form}
        name="advanced_search"
        className="ant-advanced-search-form"
        onFinish={(values: any) => {
          const copy = { ...values };
          console.log(copy);
          Object.keys(copy).forEach((el) => {
            if (copy[el] === undefined || copy[el] === null || copy[el] === '') {
              delete copy[el];
            } else if (el === 'time' && copy[el].length) {
              copy.beginTime = `${formatToDate(copy[el][0])} 00:00:00`;
              copy.endTime = `${formatToDate(copy[el][1])} 23:59:59`;
              delete copy[el];
            } else if (el === 'order' && copy[el].length) {
              // eslint-disable-next-line prefer-destructuring
              copy[copy[el][0]] = copy[el][1];
              delete copy[el];
            }
          });
          onFinish(copy);
        }}
      >
        <Row>
          <Col span={8} style={{ paddingRight: 60 }}>
            <Form.Item name="simpleSearch" label="搜索查询">
              <Input allowClear placeholder="患者手机号/订单号/处方号查询" />
            </Form.Item>
          </Col>
          <Col span={8} style={{ paddingRight: 60 }}>
            <Form.Item name="time" label="日期查询">
              <RangePicker placeholder={['起始日期', '结束日期']} />
            </Form.Item>
          </Col>
          <Col span={8} style={{ textAlign: 'right' }}>
            <Button
              onClick={() => {
                form.resetFields();
                onFinish({});
              }}
            >
              重置
            </Button>
            <Button style={{ margin: '0 16px' }} type="primary" htmlType="submit">
              查询
            </Button>
            <span
              style={{ cursor: 'pointer' }}
              onClick={() => {
                setExpand(!expand);
                setMaxHeight(expand ? '78px' : 'auto');
              }}
            >
              {expand ? <UpOutlined /> : <DownOutlined />}
            </span>
          </Col>
          <Col span={8} style={{ paddingRight: 60 }}>
            <Form.Item name="toName" label="患者姓名">
              <Input allowClear placeholder="请输入患者姓名" />
            </Form.Item>
          </Col>
          <Col span={8} style={{ paddingRight: 60 }}>
            <Form.Item name="chainId" label="订单来源">
              <Select
                // mode="multiple"
                style={{ width: '100%' }}
                placeholder="请选择"
                allowClear
                maxTagCount="responsive"
              >
                {chains.map((el: any) => (
                  <Option value={el.id} key={el.id}>
                    {el.name}
                  </Option>
                ))}

                {/* <Option key="2">银川互联网医院</Option>
                <Option key="3">3</Option>
                <Option key="5">5</Option>
                <Option key="6">6</Option> */}
              </Select>
            </Form.Item>
          </Col>
          <Col span={8} style={{ paddingRight: 60 }}>
            <Form.Item name="order" label="订单状态">
              <Cascader
                className="cascader"
                options={options}
                displayRender={displayRender}
                placeholder="请选择"
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </div>
  );
};

export default SearchForm;
